form{
	width:60%;
	margin-left:37px;//auto;
	display:table;
	flex-direction:column;
	h3{

	}
	span{
		display:inline-block;
	}
	.group_field{
		display:flex;
		flex-direction:row;
		//justify-content:space-between;
		.contain:nth-child(2){
			span{
				margin-left:20px;
			}
		}
	}
	input{
		display:table-cell;
	}
	.contain{
		display:flex;
		flex-direction:row;
		margin-bottom:10px;
	}
	input[type=submit]{
		margin:20px 0;
		outline:none;
		border:none;
		background-color:black;
		color:white;
		padding:10px;
		width:100px;
	}
}